<template>
  <!-- 选择部门 -->
  <div class="select-department">
    <navBar :svgIcon='svgIcon' :titleText="'选择部门'" @changeClose='changeClose'></navBar>
    <van-cell v-for="(item) in departmentList" :key="item.id" is-link @click="showPopup(item)">{{item.label}}</van-cell>
  </div>
</template>

<script>
import navBar from '@/components/navBar.vue'
export default {
  name: 'SelectDepartment',
  props: {
    svgIcon: {
      type: String,
      default: ()=> require('../assets/left_back.svg')
    },
  },
  components: {
    navBar
  },
  data() {
    return {
      departmentList: [],
    }
  },
  methods: {
    changeClose() {
      this.$emit('changeClose')
    },
    showPopup(val) {
      this.$store.commit('changeOrganizationId', val.id)
      if(this.svgIcon.includes('close')) {
        this.$emit('changeClose');
      } else {
        // debugger
        this.$router.push({name: 'SelectDevice'})
        // this.$router.push({name: 'SelectDevice', query: {organizationId: val.id}})
      }
      this.$store.commit('changeDepartment', val.label)
    },
  },
  mounted() {
    this.$getHttp(`${this.$baseUrl}/OS-PM/organization/getTree`).then(res=> {  //  获取部门
      // debugger
      if (res.status == 200) {
         function flat(arr) {
          // debugger
          let result = [];
          arr.forEach(item=> {
            if (item.children.length != 0) {
              result.push(item)
              result = result.concat(flat(item.children))
            } else {
              result.push(item)
            }
          })
          return result
        }
        // debugger
        this.departmentList = flat(res.data)
      }
    }).catch(err => {
      // debugger
    })
  }
}
</script>

<style lang='less' scoped>
  .select-department {
    .van-icon-arrow:before {  //  箭头颜色
      color: #1F4BF4;
    }
  }
</style>